export default {
	data() {
		return {
			canUse:false,
			xinpiao:''
		};
	},
	methods:{
		changeVal(event){
			let val = event.target.value
			this.xinpiao=val
			if(val){
				this.canUse=true
			}else{
				this.canUse=false
			}
		},
		sureBtn(){
			if(!this.canUse){
				this.$acFrame.Util.mytotal('请输入兑换的星票数量')
			}else{
				uni.navigateTo({
					url: 'cashOutSucces',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
}
